.about__container{
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}
.about__me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg ,transparent , var(--color-primary), transparent);
    display: grid;
    place-items: center;
}
.about__me-image{
    border-radius: 2rem;
    overflow: hidden;
}

.about__cards{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 1.5rem;
}
.about__card{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}
.about__card:hover{
    background-color: transparent;
    border-color: var(--color-primary-variant);
}
.about__icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem
}
.about__card h5{
    font-size: 1.5rem;
}
.about__card small{
    font-size: 0.7rem;
    color: var(--color-light);
}
.about_content p{
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}
.hideen{
    visibility: hidden;
}


/* meduim */
@media (max-width:1024px){
    .about__container{
        grid-template-columns: 1fr;
    }
    .about__me{
        width: 50%;
        margin: 2rem auto;
    }
    .about_content p{
        margin: 1rem 0 1.5rem;
    }

}
/* small */
@media (max-width:776px){
    .about__me{
        width: 65%;
    }
    .about__cards{
        grid-template-columns: 1fr;
        width: 60%;
        margin: 0 auto;
    }
    .about_content{
        text-align: center;
        margin: 1rem 0 4rem;
    }
    .about_content p{
        width: 70%;
        margin: 4rem auto 2rem;
    }
}


